import React, { useRef, useEffect } from "react";

export default ({
  name,
  label,
  value,
  disabled,
  onChange,
  id,
  children,
  defaultOption
}) => {
  const ref = useRef();
  useEffect(() => {
    console.log({ ref })
  });
  return (
    <div className="field">
      <label htmlFor={name}>{label}</label>
      <div className="select-container" onClick={() => ref.current && ref.current.click()}>
        <select id={id} name={name} value={value} onChange={onChange} disabled={disabled} ref={ref}>
          {defaultOption && <option value="" hidden>{defaultOption}</option>}
          {children}
        </select>
        <div className="select-arrow">
          <img width="12" height="12" alt="" src="data:image/svg+xml;base64," />
        </div>
      </div>
    </div>
  );
}
